<template>
    <div class="abouts">
        <head-top></head-top> 
        <div class="main">
            <div class="lt">
                <div class="avatar">
                   <a href="#"><span></span></a>
                </div>
                <div class="topspaceinfo">
                  <h1></h1>
                  <p>..</p>
                </div>
                <div class="userinfo"> 
                  <p class="q-fans"> <a>220</a></p>   
                </div>
                <div class="newpic">
                    <h2>{{info.title}}</h2>
                    <p>{{info.desc}}</p>
                </div>
                <div class="newpic">
                    <h2>{{info.username}}</h2>
                    <div class="userinfo">
                       <div><span>:</span>{{info.netname}}</div>
                       <div><span>QQ:</span>{{info.qq}}</div>
                       <div><span>:</span>{{info.profession}}</div>
                       <div><span>:</span>{{info.hobby}}</div>
                    </div>
                </div>
            </div>
            <div class="rt">
               <h2 class="title"></h2>
               <ul class="text">
                  <p><span></span>90IT~W_W~</p>
                  <p><span></span></p>
                  <p><span></span>90IT boy(...)IT2""htmlcssjsphp......""</p>
                  <p><span></span>: {{info.program}}</p>
                </ul>
            </div>
        </div>
        <head-foot></head-foot>
    </div>
</template>
<script type="text/javascript">
    import headTop from './public/HeadTop';
    import headFoot from './public/HeadFoot';
    import { baseUrl } from '../config/env';
    import { getInfo, addAboutHit, getSayList } from '../api/getData';
    export default{
        data(){
            return {
                info: {},
                baseUrl,
                num: 0,
                page: 4,
                imgList: [],
                keyword: []
            }
        },
        components: {headTop, headFoot},
        created(){
           this.getData();
        },
        methods: {
           async getData(){
             Promise.all([ getInfo(), getSayList({num: this.num, page: this.page}) ]).then(res => {
                for(var i = 0; i < res.length; i++){
                   res[i] = JSON.parse(res[i]);
                }
                if(res[0].errcode == 0){
                   res[0].data[0]['keyword'] = res[0].data[0]['keyword'].split(',');
                   this.info = res[0].data[0];
                }
                if(res[1].errcode == 0){
                   for(var i = 0; i < res[1].data.length; i++){
                     res[1].data[i]['s_img'] = res[1].data[i]['s_img'] ? res[1].data[i]['s_img'] : '';
                   }
                   this.imgList = res[1].data;
                }
             })
           }
        }
    }
</script>


